import { Button, Col, Divider, Layout, Row, Typography, message } from "antd";
import React from 'react';
import { App, DB_INIT } from '../../app.js';

const { Content } = Layout;
const { Text } = Typography;

class InitPage extends React.Component {

    constructor(props) {
        super(props)
        this.state = {}
        this.testViewVisibility = 'hidden'
        if(!App.isProduction()) {
            this.testViewVisibility = 'visible'
        }
    }

    async refreshDB(files) {
        await App.initDb(files[0])
        this.props.onDbReady()
    }

    async create() {
        await App.createDb()
        this.props.onDbReady()
    }

    export() {
        if (!DB_INIT) {
            message.error("DB")
            return
        }
        App.db?.export()
    }

    click1() {
    }

    click2() {
    }

    render() {
        return (
            <Content className='Content'>
                <Divider orientation="center">{App.getVersion()}</Divider>
                <Row style={{ padding: '10px 50px 50px', backgroundColor: "#eee" }}>
                    <Col span={8} align='center'>
                        <Divider orientation="center">DB</Divider>
                        <Button onClick={() => this.create()}></Button>
                    </Col>
                    <Col span={8} align='center'>
                        <Divider orientation="center">DB</Divider>
                        <Text>{this.state.fileName}</Text>
                        <input type='file' id='dbfile' accept=".db" onChange={(e) => this.refreshDB(e.target.files)} />
                    </Col>
                    <Col span={8} align='center'>
                        <Divider orientation="center">DB</Divider>
                        <Button onClick={() => this.export()}></Button>
                    </Col>
                </Row>
                <Row style={{ padding: '10px 50px 50px', backgroundColor: "#eee", margin: "10px 0", visibility: this.testViewVisibility }}>
                    <Divider orientation="center"></Divider>
                    <Button onClick={() => this.click1()}>click1</Button>
                    <Button onClick={() => this.click2()}>click2</Button>
                </Row>
            </Content>
        )
    }
}

export default InitPage