import { UserConfig, UserConfigStatus, UserConfigType } from "../entity/user_entity";
import { UserRepo } from "../repo/user_repo";
import { IncomeExpenditureService } from "./income_expenditure_service";

class UserService {

    static repo = new UserRepo()

    static initDefaultTypes() {
        for(const type of V0IncomeExpenditureType.values()) {
            let config = new UserConfig();
            config.type = type.isIncome() ? UserConfigType.IncomeType : UserConfigType.ExpenditureType;
            config.status = UserConfigStatus.Normal;
            config.code = type.code;
            config.name = type.name;
            config.parent_code = type.getParentCode();
            this.repo.upsert(config);
        }
    }

    /**
     * @param {UserConfig} config 
     */
    static save(config) {
        this.repo.upsert(config);
        if(config.type == UserConfigType.IncomeType || config.type == UserConfigType.ExpenditureType) {
            IncomeExpenditureService.refreshTypes(true);
        }
    }

    /**
     * @param {UserConfigType} type
     * @returns {Array[UserConfig]} 
     */
    static getAll(type) {
        return this.repo.selectType(type);
    }
}

class V0IncomeExpenditureType {
    static Incomme = {
        salary: {
            self: new V0IncomeExpenditureType(10000, ""),
            num13: new V0IncomeExpenditureType(10001, "-"),
            bonus: new V0IncomeExpenditureType(10002, "-"),
        },
        luckmoney: {
            self: new V0IncomeExpenditureType(20000, ""),
            work: new V0IncomeExpenditureType(20001, "-"),
            home: new V0IncomeExpenditureType(20002, "-"),
        },
        other: {
            self: new V0IncomeExpenditureType(30000, ""),
        }
    };

    static Expenditure = {
        home: {
            self: new V0IncomeExpenditureType(-10000, ""),
            rent: new V0IncomeExpenditureType(-10001, "-"),
            utility: new V0IncomeExpenditureType(-10002, "-"),
        },
        daily: {
            self: new V0IncomeExpenditureType(-20000, ""),
            taxi: new V0IncomeExpenditureType(-20001, "-"),
            phone: new V0IncomeExpenditureType(-20002, "-"),
        },
        shopping: {
            self: new V0IncomeExpenditureType(-30000, ""),
            elc: new V0IncomeExpenditureType(-30001, "-"),
            life: new V0IncomeExpenditureType(-30002, "-"),
        },
        happy: {
            self: new V0IncomeExpenditureType(-40000, ""),
            online: new V0IncomeExpenditureType(-40001, "-"),
            offline: new V0IncomeExpenditureType(-40002, "-"),
        },
        food: {
            self: new V0IncomeExpenditureType(-50000, ""),
            canteen: new V0IncomeExpenditureType(-50001, "-"),
            out: new V0IncomeExpenditureType(-50002, "-"),
        },
        family: {
            self: new V0IncomeExpenditureType(-60000, ""),
            traval: new V0IncomeExpenditureType(-60001, "-"),
            gift: new V0IncomeExpenditureType(-60002, "-"),
        },
        study: {
            self: new V0IncomeExpenditureType(-70000, ""),
            online: new V0IncomeExpenditureType(-70001, "-"),
            offline: new V0IncomeExpenditureType(-70002, "-"),
        },
        health: {
            self: new V0IncomeExpenditureType(-80000, ""),
        },
        car: {
            self: new V0IncomeExpenditureType(-90000, ""),
            etc: new V0IncomeExpenditureType(-90001, "-/ETC"),
            oil: new V0IncomeExpenditureType(-90002, "-"),
        },
        girlfriend: {
            self: new V0IncomeExpenditureType(-100000, ""),
            traval: new V0IncomeExpenditureType(-100001, "-"),
            eat: new V0IncomeExpenditureType(-100002, "-"),
            gift: new V0IncomeExpenditureType(-100003, "-"),
        },
        other: {
            self: new V0IncomeExpenditureType(-110000, ""),
        },
    }

    constructor(code, name) {
        this.code = code
        this.name = name
    }

    static getByCode(code) {
        for(const type of this.values()) {
            if(type.code == code) {
                return type
            }
        }
        return null
    }

    static toList(data) {
        if(data instanceof V0IncomeExpenditureType) {
            return [data]
        } else {
            let keys = Object.keys(data)
            let result = []
            for(const key of keys) {
                result = result.concat(this.toList(data[key]))
            }
            return result
        }
    }

    /**
     * @returns {Array[V0IncomeExpenditureType]}
     */
    static values() {
        return this.toList(this.Incomme).concat(this.toList(this.Expenditure))
    }

    getParentCode() {
        let parentCode = this.code - this.code % 100;
        if(parentCode === this.code) {
            return null;
        } else {
            return parentCode;
        }
    }

    isIncome() {
        return this.code > 0
    }
}

export { UserService };
